p {
  margin: 0.5rem;
}

.flashcards {
  height: 100vh;
  background-color: sandybrown;
}

.card-container {
  padding-top: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  text-align: left;
}

button {
  border: none;
  background-color: Transparent;
  height: fit-content;
  width: 100%;
}

.arrow-button {
  width: 10vw;
  p {
    margin: 1rem;
    font-size: 6rem;
  }
  :hover {
    font-size: 6.5rem;
  }
}

.flip {
  transform: rotateY(180deg);
}

.flip-card {
  height: 50vh;
  perspective: 1000px;
}

.flip-card-inner {
  height: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-front {
  background-color: white;
  color: black;
}

.flip-card-back {
  background-color: #2980b9;
  color: white;
  transform: rotateY(180deg);
}
