@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
.App{margin-left:calc(100vw - 100%);margin-right:0;font-family:'Open Sans', sans-serif;font-size:14px;-webkit-font-smoothing:antialiased !important}

.navbar-brand{font-weight:600;letter-spacing:1px;text-transform:uppercase}.navbar-nav .nav-item{letter-spacing:1px;margin-left:20px;text-align:center;text-transform:uppercase;cursor:pointer}.navbar-nav .nav-item:hover{color:#ffc952 !important}.navbar-nav a{color:#212529 !important;display:inline;font-size:1rem}.navbar-nav a:hover{color:#ffc952 !important}.navbar-nav svg{font-size:2em}

.tab-pane .active{display:flex !important;justify-content:center}.nav-link:hover{cursor:pointer}

.acknowledgements{justify-content:center}.acknowledgements .row{justify-content:center}.acknowledgements h1{text-align:center}.acknowledgements .nav-link{padding:0}

.table .row{justify-content:center}.table thead>tr:first-child{display:none}.table thead>tr th{background:white;position:-webkit-sticky;position:sticky;top:0}.table .nav-link{padding:0}.table .easy{background-color:#5cb85c}.table .medium{background-color:#f0ad4e}.table .hard{background-color:#d9534f}.table .companies,.table .patterns{justify-content:space-evenly;margin:0}.table .react-toggle{top:7px}

.tips{text-align:center !important;display:block;overflow-x:auto;padding:0.5em;color:#333;background:#f8f8f8}

p{margin:0.5rem}.flashcards{height:100vh;background-color:sandybrown}.card-container{padding-top:20vh;display:flex;justify-content:center;align-items:center}.card-content{display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:100%;text-align:left}button{border:none;background-color:Transparent;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;width:100%}.arrow-button{width:10vw}.arrow-button p{margin:1rem;font-size:6rem}.arrow-button :hover{font-size:6.5rem}.flip{transform:rotateY(180deg)}.flip-card{height:50vh;perspective:1000px}.flip-card-inner{height:100%;transition:transform 0.6s;transform-style:preserve-3d;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.flip-card-front,.flip-card-back{position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden}.flip-card-front{background-color:white;color:black}.flip-card-back{background-color:#2980b9;color:white;transform:rotateY(180deg)}

